  transition: height 2s ease-in;
  margin: auto;
  color: white;
  background: #616161;
  border-radius: 10px;
  font-family: 'Fira Sans', sans-serif;
  box-shadow: 2px 6px 20px rgba(0, 0, 0, 0.5);
  display: grid;
  grid-template: 0.6fr 2fr auto / 18em 1fr 1.4fr;
  align-items: start;
